import React from "react";
import { RouteComponentProps } from "react-router-dom";
// import { RouteComponentProps } from "react-router";
import Navigation from "../home/navigation";
import tab from "./../common/data";
import searchData from "./../common/searchData";

import Footer from "../home/footer";
interface ComponentProps {}
type Props = ComponentProps & RouteComponentProps;

class AboutPage extends React.PureComponent<Props> {
  render() {
    const params: any = this.props.match.params;
    const tabKey = params.tab;
    const id = params.id;
    const data = searchData(tab, tabKey, id);
    console.log("DATA", data);
    return (
      <body>
        <Navigation />
        {/* <!-- inner page banner --> */}
        <section className="innerpage_banner py-5">
          <div className="dot1"></div>
        </section>
        {/* <!-- //inner page banner --> */}

        {/* <!-- about properties --> */}
        <section className="banner_bottom py-5">
          <div className="container">
            <div className="heading">
              <h2 className="heading text-capitalize mb-5 pb-5">
                <span> {data[0].title}</span>
              </h2>
              <h4 className="position cont">{data[1].label}</h4>
            </div>
            <div className="inner_sec_info_agile_w3_info">
              <div className="help_full">
                <ul className="rslides" id="slider4">
                  <li>
                    <div className="respon_info_img">
                      <img
                        src={data[1].images}
                        className="img-responsive"
                        alt="Estate Land"
                      />
                    </div>
                    <div className="banner_bottom_left">
                      <h4 className="my-3 text-capitalize">
                        {data[1].propertyName}
                      </h4>
                      <ul className="property-info mb-3">
                        <li>
                          <span className="fas fa-building"></span>{" "}
                          {data[1].propertyArea}
                        </li>
                        <li>
                          <span className="fas fa-bed"></span>{" "}
                          {data[1].bedrooms}
                        </li>
                        <li>
                          <span className="fas fa-bath"></span>{" "}
                          {data[1].bathrooms}
                        </li>
                        <li>
                          <span className="fas fa-car"></span>{" "}
                          {data[1].parkingArea}
                        </li>
                      </ul>
                      <p>{data[1].info}</p>
                      <div className="ab_button">
                        <a
                          className="btn btn-primary btn-lg hvr-underline-from-left"
                          href={`${process.env.PUBLIC_URL}/properties/${params.tab}/${params.id}/booking`}
                          role="button"
                        >
                          Read More{" "}
                        </a>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </section>
        {/* <!-- //about properties --> */}

        {/* <!-- /newsletter--> */}
        <section className="newsletter py-5">
          <div className="container">
            <div className="newslettergrids row">
              <div className="col-md-6 mb-md-0 mb-4 newsleft">
                <h3>Sign up for Newsletter !</h3>
              </div>
              <div className="col-md-6 newsright">
                <form action="#" method="post">
                  <input
                    type="email"
                    placeholder="Enter your email..."
                    name="email"
                    required={true}
                  />
                  <input type="submit" value="Submit" />
                </form>
              </div>
            </div>
          </div>
        </section>
        {/* <!-- //newsletter--> */}

        <Footer />
      </body>
    );
  }
}
export default AboutPage;
