// var jQuery = require( "jquery" );
import jQuery from "jquery";
import "responsive-slides";
jQuery(function () {
  // Slideshow 4
  jQuery("#slider4").responsiveSlides({
    auto: true,
    pager: false,
    nav: true,
    speed: 500,
    namespace: "callbacks",
    before: function () {
      jQuery(".events").append("<li>before event fired.</li>");
    },
    after: function () {
      jQuery(".events").append("<li>after event fired.</li>");
    },
  });
});
