import React, { Component } from "react";
interface ComponentProps {}
type Props = ComponentProps;
class Navigation extends Component<Props> {
  render() {
    return (
      <div className="top-nav">
        <div className="container-fluid">
          <nav className="navbar navbar-expand-lg navbar-light">
            <a
              className="navbar-brand text-uppercase"
              href={`${process.env.PUBLIC_URL}/`}
            >
              Assystant <i className="fas fa-building"></i>
              <span>Real Property</span>
            </a>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>

            <div
              className="collapse navbar-collapse justify-content-center pr-md-4"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <a
                    className="nav-link active"
                    href={`${process.env.PUBLIC_URL}/`}
                  >
                    Home <span className="sr-only">(current)</span>
                  </a>
                </li>
                <li className="nav-item">
                  <a
                    className="nav-link"
                    href={`${process.env.PUBLIC_URL}/about`}
                  >
                    About
                  </a>
                </li>
                <li className="nav-item">
                  <a
                    className="nav-link"
                    href={`${process.env.PUBLIC_URL}/services`}
                  >
                    Services
                  </a>
                </li>
                <li className="nav-item">
                  <a
                    className="nav-link"
                    href={`${process.env.PUBLIC_URL}/property`}
                  >
                    Properties
                  </a>
                </li>
                <li className="dropdown nav-item">
                  <a
                    href="#"
                    className="dropdown-toggle nav-link"
                    data-toggle="dropdown"
                  >
                    Pages
                    <b className="caret"></b>
                  </a>
                  <ul className="dropdown-menu agile_short_dropdown">
                    <li>
                      <a href={`${process.env.PUBLIC_URL}/single`}>
                        Single Page
                      </a>
                    </li>
                    <li>
                      <a href={`${process.env.PUBLIC_URL}/history`}>History</a>
                    </li>
                  </ul>
                </li>
                <li className="nav-item">
                  <a
                    className="nav-link"
                    href={`${process.env.PUBLIC_URL}/contact`}
                  >
                    Contact
                  </a>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}
export default Navigation;
