import React, { Component } from "react";
import "react-stepzilla/src/css/main.css";
import StepZilla from "react-stepzilla";
// import Ridge from "../ridge";
// import Park from "../park";
import UnitImage from "../displayView/displayImage";
import tab from "./../common/data";
import "./style.css";
import PropertyView from "../propertyView";
import RidgeData from "./ridgeData";
import ParkData from "./parkData";
import Footer from "../home/footer";
import Navigation from "../home/navigation";
import InnerBanner from "../common/innerBanner";
import ContactInfo from "../common/infoForm";
import Details from "../common/unitDetails";
import { RouteComponentProps } from "react-router-dom";
import searchData from "./../common/searchData";
import { Modal, Button } from "react-bootstrap";
interface ComponentProps {
  history: any;
}
//TODO: State to store ridge or park selection
interface State {
  toggle: string;
  formData: any;
  show: boolean;
  hide: boolean;
}
type Props = ComponentProps & RouteComponentProps;

class DashBoard extends Component<Props, State> {
  state: State = {
    toggle: "",
    formData: {},
    show: false,
    hide: false,
  };
  gotoRidge = () => {
    this.props.history.push("/unitImage");
  };
  moveToUnit = (value: string) => {
    this.setState({ toggle: value });
  };
  navigateToStepThree = () => {
    // @ts-ignore
  };
  setFormData = (item: any) => {
    this.setState({ formData: item });
  };

  showModal = () => {
    const handleClose = () => {
      this.setState({
        show: false,
      });
    };
    const handleShow = () => {
      this.setState({
        show: true,
      });
    };

    return (
      <div
        style={{
          justifyContent: "center",
          // alignItems: "center",
          backgroundColor: "red",
        }}
      >
        {/* <Button
          variant="primary"
          onClick={() => {
            handleShow();
          }}
        >
          Launch demo modal
        </Button> */}

        <Modal show={this.state.show} onHide={handleClose} animation={true}>
          <Modal.Header closeButton>
            <Modal.Title>Contact Info</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <section className="contact_form py-5">
              <div className="container">
                <div className="heading">
                  <h1 className="heading text-capitalize mb-5 pb-3">
                    <span>Contact</span> Us
                  </h1>
                  <h4 className="position cont">CONTACT</h4>
                </div>
                <form action="#" method="post">
                  <div className="input-w3ls w3ls-left">
                    <input
                      type="email"
                      className="name"
                      name="Email"
                      placeholder="Email"
                      required={true}
                    />
                  </div>
                  <div className="input-w3ls">
                    <input type="submit" value="Save" />
                  </div>
                </form>
              </div>
            </section>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={handleClose}>
              Save
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  };

  render() {
    const params: any = this.props.match.params;
    // console.log("PROPS DATA", params);
    const tabKey = params.tab;
    const id = params.id;
    const data = searchData(tab, tabKey, id);
    const blockData = data[1].blocks;
    const bkgImg = data[1].backgroundImage;
    const viewBox = data[1].viewBox;
    // const data1 = data[1];
    console.log("DATA", data[1]);
    const { toggle } = this.state;
    const steps = [
      {
        name: "Select Block",
        component: (
          <UnitImage
            renderProperScreen={this.moveToUnit}
            blockData={blockData}
            bkgImg={bkgImg}
            viewBox={viewBox}
          />
        ),
      },
      // { name: "Fill Details", component: <DetailsForm /> },
      {
        name: "Select Unit",
        component: (
          <PropertyView
            data={toggle === "ridge" ? RidgeData : ParkData}
            setFormData={this.setFormData}
            bkgImg={bkgImg}
            viewBox={viewBox}
          />
        ),
      },
      {
        name: "Property Details ",
        component: <Details data={data} />,
      },

      {
        name: " Fill Contact Info",
        component: <ContactInfo />,
      },
    ];
    return (
      <>
        <Navigation />
        <InnerBanner />

        <div className="step-progress">
          <StepZilla
            steps={steps}
            showNavigation={false}
            showSteps={true}
            prevBtnOnLastStep={false}
            stepsNavigation={false}
          />
        </div>
        <div style={{ alignItems: "center", justifyContent: "center" }}>
          <Button
            variant="primary"
            onClick={() => {
              this.showModal();
            }}
          >
            demo modal
          </Button>
        </div>
        <Footer />
      </>
    );
  }
}

export default DashBoard;
