import React, { Component } from "react";
import { Button } from "react-bootstrap";
// import { RouteComponentProps } from "react-router-dom";

interface ComponentProps {
  // propertyDetails: () => void;
  data: any;
}
class Details extends Component<ComponentProps> {
  nextStep = () => {
    // @ts-ignore
    this.props.jumpToStep(3);
  };
  render() {
    const data = this.props.data[1];
    return (
      <div className="banner_bottom read_page py-5 my-md-5">
        <div className="container">
          <div className="heading">
            <h1 className="heading text-uppercase mb-5 pb-3">
              <span>Property</span> Description
            </h1>
            <h4 className="position cont">DESCRIPTION</h4>
          </div>
          <div className="inner_sec_info_agile_w3_info">
            <div className="read_img">
              <img src={data.images} alt=" " className="img-responsive" />
              <ul className="property-info mt-4">
                <li>
                  <span className="fas fa-building"></span> {data.propertyArea}
                </li>
                <li>
                  <span className="fas fa-bed"></span> {data.bedrooms}
                </li>
                <li>
                  <span className="fas fa-bath"></span> {data.bathrooms}
                </li>
                <li>
                  <span className="fas fa-car"></span> {data.parkingArea}
                </li>
              </ul>
              <p className="para mt-2">
                <span className="fas fa-map-marker-alt"></span>
                <strong>Address : </strong> {data.address}
              </p>
              <p className="read_para">{data.info}</p>
            </div>
            <div className="ab_button">
              {/* <a
                className="btn btn-primary btn-lg"
                href="/contactForm"
                role="button"
              >
                Contact us
              </a> */}
              <Button onClick={this.nextStep}>Contact Us</Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Details;
