import React, { Component } from "react";
import { Tabs, Tab, Container, Row } from "react-bootstrap";
import p1 from "../../assets/web/p1.jpg";
import tab from "./data";

import Property from "../history/property";
interface Props {}
class TabView extends Component<Props> {
  render() {
    return (
      <section style={{ marginBottom: 75, marginTop: 50 }}>
        <Container>
          <Tabs
            defaultActiveKey="land"
            id="uncontrolled-tab-example"
            className="pilled-selection"
          >
            {tab.map((item) => {
              return (
                <Tab eventKey={item.eventKey} title={item.title}>
                  <Property
                    propertyData={item.property}
                    tab={item.eventKey}
                  />
                </Tab>
              );
            })}
          </Tabs>
        </Container>
      </section>
    );
  }
}
export default TabView;
