import React, { Component } from "react";
import "react-stepzilla/src/css/main.css";
import StepZilla from "react-stepzilla";
import DetailsForm from "../detailsForm";
import "./style.css";
import WingView from "../wingView";
import ProvidentUnitImage from "../providentUnit";
import WingA from "./wingA";
import WingB from "./wingB";
import WingC from "./wingC";
import WingD from "./wingD";
import providentWingA from "./../../assets/provident_wing_a.jpg";
import providentWingB from "./../../assets/provident_wing_b.jpg";
import providentWingC from "./../../assets/provident_wing_c.jpg";
import providentWingD from "./../../assets/provident_wing_d.jpg";
import Footer from "../home/footer";
import InnerBanner from "../common/innerBanner";
import Navigation from "../home/navigation";
import ContactInfo from "../common/infoForm";
import Details from "../common/unitDetails";

interface props {
  history: any;
}
interface State {
  toggle: string;
  formData: any;
}
class ProvidentDashBoard extends Component<props, State> {
  state: State = {
    toggle: "",
    formData: {},
  };
  // gotoRidge = () => {
  //   this.props.history.push("/unitImage");
  // };
  moveToUnit = (value: string) => {
    this.setState({ toggle: value });
  };
  propertyDetails = () => {};
  // setFormData = (item: any) => {
  //   this.setState({ formData: item });
  // };
  setFormData = (item: any) => {
    this.setState({ formData: item });
  };
  render() {
    const { toggle } = this.state;
    let data = [];
    var bckImg;
    switch (toggle) {
      case "wingA":
        data = WingA;
        bckImg = providentWingA;
        break;
      case "wingB":
        data = WingB;
        bckImg = providentWingB;
        break;
      case "wingC":
        data = WingC;
        bckImg = providentWingC;
        break;
      default:
        data = WingD;
        bckImg = providentWingD;
        break;
    }
    const steps = [
      {
        name: "Select Wing",
        component: <ProvidentUnitImage renderProperScreen={this.moveToUnit} />,
      },
      // { name: "Fill Details", component: <DetailsForm /> },
      {
        name: "Select Unit",
        component: (
          <WingView
            data={data}
            setFormData={this.setFormData}
            wingBack={bckImg}
          />
        ),
      },
      // {
      //   name: "Property Details ",
      //   component: <Details />,
      // },
      {
        name: "Fill Contact Info",
        component: <ContactInfo />,
      },
      // {
      //   name: "Select Property",
      //   component: (
      //     <WingView
      //       data={
      //         toggle === "wingA" ? <WingA /> : <WingA /> ? <WingA /> : <WingA />
      //       }
      //       setFormData={this.renderWing}
      //     />
      //   ),
      // },
      //   {
      //     name: "Fill Details",
      //     // component: <DetailsForm formData={this.state.formData} />,
      //   },
    ];
    return (
      // <div
      //   style={{
      //     //   justifyContent: "center",
      //     alignItems: "center",
      //     height: "100%",
      //     width: "100%",
      //   }}
      // >
      <>
        <Navigation />
        <InnerBanner />
        <div className="step-progress">
          <StepZilla
            steps={steps}
            showNavigation={false}
            showSteps={true}
            prevBtnOnLastStep={false}
            stepsNavigation={false}
          />
        </div>
        <Footer />
      </>
    );
  }
}

export default ProvidentDashBoard;
