import React, { Component } from "react";
import { Row, Col } from "react-bootstrap";
interface Props {
  propertyData: Array<any>;
  tab: string;
}
class Property extends Component<Props> {
  render() {
    const { tab } = this.props;
    return (
      <section className="property" style={{ marginTop: 35 }}>
        <Row>
          {this.props.propertyData.map((item) => {
            return (
              <Col lg={4} sm={6} className="mb-lg-0 mb-5 team-grid">
                <div className="team-img">
                  <img src={item.images} className="img-responsive" alt=" " />
                  <figcaption className="overlay">
                    <a
                      href={`${process.env.PUBLIC_URL}/properties/${tab}/${item.key}`}
                    >
                      Know More
                    </a>
                  </figcaption>
                </div>
                <div className="prop-info-w3ls">
                  <div className="both-w3-agileits">
                    <p>
                      <i className="fas fa-building" aria-hidden="true"></i>
                      {item.propertyArea}
                    </p>
                  </div>
                  <div className="both-w3-agileits">
                    <p>
                      <i className="fa fa-bed" aria-hidden="true"></i>{" "}
                      {item.bedrooms}
                    </p>
                  </div>
                  <div className="both-w3-agileits">
                    <p>
                      <i className="fas fa-bath" aria-hidden="true"></i>{" "}
                      {item.bathrooms}
                    </p>
                  </div>
                  <div className="both-w3-agileits">
                    <p>
                      <i className="fas fa-car" aria-hidden="true"></i>{" "}
                      {item.parkingArea}
                    </p>
                  </div>
                  <div className="clearfix"> </div>
                </div>
                <div className="prop-btm-w3ls">
                  <h4>
                    Property Name <span>{item.propertyName}</span>
                  </h4>
                </div>
              </Col>
            );
          })}
        </Row>
      </section>
    );
  }
}
export default Property;
